<template>
  <img alt="Vue logo" src="./assets/logo.png" />
  <br />
  <button v-on:click="showData1">คลิกเพื่อดู</button>
  <button @click="showData2">คลิกเพื่อดู</button>
  <h1>{{ firstname }}</h1>
  <h2>{{ lastname }}</h2>

  <h3>{{ age }}</h3>
  <br />
  <button @click.right="plus1">เพิ่มอายุ</button>
  <button @click="plus2">ลดอายุ</button>
  <br />
  <button @click="pluss1(5)">บวก5</button>
  <button @click="pluss2(5)">ลบ5</button>

  <form>
    <label for="">ป้อนชื่อจริง</label>
    <input type="text" v-on:input="setfirstname1" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>

  <h2>แสดงชื่อจริง : {{ firstname1 }}</h2>

  <form>
    <label for="">ป้อนนามสกุล</label>
    <input type="text" v-on:input="setlastname1" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>

  <h2>แสดงนามสกุล : {{ lastname1 }}</h2>

  <form>
    <label for="">ป้อนชื่อเล่น</label>
    <input type="text" v-on:input="setnickname" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>

  <h2>แสดงชื่อเล่น : {{ nickname }}</h2>

  <form>
    <label for="">ป้อนอายุ</label>
    <input type="text" v-on:input="setage1" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>

  <h2>แสดงอายุ : {{ age1 }}</h2>

  <form>
    <label for="">ป้อน วัน/เดือน/ปี</label>
    <input type="text" v-on:input="setday" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>
  <h2>แสดงวัน/เดือน/ปี : {{ day }}</h2>

  <form>
    <label for="">ป้อน สาขา</label>
    <input type="text" v-on:input="setmajor" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>

  <h2>แสดงชื่อสาขา : {{ major }}</h2>

  <form>
    <label for="">ป้อน ห้องเรียน</label>
    <input type="text" v-on:input="setroom" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>

  <h2>แสดงห้องเรียน : {{room}}</h2>

  <form>
    <label for="">ป้อน คติประจำใจ</label>
    <input type="text" v-on:input="sett" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>

  <h2>แสดงคติประจำใจ : {{t}}</h2>

  <form>
    <label for="">ป้อน สถานที่เรียนต่อ</label>
    <input type="text" v-on:input="sety" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>

  <h2>แสดงสถานที่เรียนต่อ : {{y}}</h2>

  <form>
    <label for="">ป้อน วิชาที่ไม่ชอบ</label>
    <input type="text" v-on:input="setu" />
    <br />
    <button type="submit">กรุณากดปุ่ม</button>
  </form>

  <h2>แสดงวิชาที่ไม่ชอบ : {{u}}</h2>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      firstname: "Plonrawat",
      lastname: "Mengam",
      age: 21,
      nickname: "",
      firstname1: "",
      lastname1: "",
      age1: "",
      day: "",
      major: "",
      room: "",
      t: "",
      y: "",
      u: "",
    };
  },
  methods: {
    showData1() {
      alert(this.firstname);
    },
    showData2() {
      alert(this.lastname);
    },
    plus1() {
      this.age++;
    },
    plus2() {
      this.age--;
    },
    pluss1(val) {
      this.age += val;
    },
    pluss2(val) {
      this.age -= val;
    },
    setfirstname1(event) {
      this.firstname1 = event.target.value;
    },
    setlastname1(event) {
      this.lastname1 = event.target.value;
    },
    setnickname(event) {
      this.nickname = event.target.value;
    },
    setage1(event) {
      this.age1 = event.target.value;
    },
    setday(event) {
      this.day = event.target.value;
    },
    setmajor(event) {
      this.major = event.target.value;
    },
    setroom(event) {
      this.room = event.target.value;
    },
    sett(event) {
      this.t = event.target.value;
    },
    sety(event) {
      this.y = event.target.value;
    },
    setu(event) {
      this.u = event.target.value;
    },
  },
};
</script>

